body{
    background-color: aqua;
    display: flex;
    justify-content: center;

}



#fundo{

   position: absolute;
   
   top:0px;
   left: 0px;
   height: 100vh;
   width: 100vw;


   background-color:rgba(0, 255, 255, 0.2) ;
   backdrop-filter: blur( 1px );
-webkit-backdrop-filter: blur( 1px );
 
}

#form-criando{
   top: 20%;
   position:absolute;
   width: 500px;
    border: 2px solid black;
    background-color: white;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

 }
 #form-criando input{width: 80%;}

 #form-criando input[type="text"]{
    height: 40px;
 }

 #form-criando input[type="textarea"]{
    height: 150px;
 }

 #form-criando input[type="button"]{
   margin-bottom: 20px;
 }

@media (max-width: 499px) {
    #container{
         min-width: 300px;
         max-width: 300px;
    }
  }


#container{
    font-family: 'Lato', sans-serif;
    display: inline-block;
    text-align: center;
    width: 500px;
    min-height: 600px;
    background-color: white;
    border:1px solid black;
    border-radius: 20px;

    /* display: flex ; */
    /* flex-direction: column;
    justify-content:flex-start; */


}
button{
    font-size:50px ;
    margin-top: 30px;
    color: white;
    background-color: rgb(207, 74, 96);
    border: none;
    height: 60px;
    width: 60px;
}
