 #footer input{
    /* position: absolute;
    bottom: 20px; */
    font-size: xx-large;
    margin-top: 50px;
    width:50% ;
    height: 50px;
    background-color:rgb(207, 74, 96);
    border: none;
    color: white;



 }




.taskelements{
    font-family: 'Lato', sans-serif;
    border: 2px black solid;
    display:flex;
    margin-left: 10px;
    margin-right: 10px;
    min-height: 67px;
    align-items: center;
    margin-top: 25px;

}

.taskGeneral:hover .buttons{
    display:flex;
    justify-content: center;
    align-items: center;

}
.taskGeneral:hover .descricao{
    display: block;
    margin-left: 10px;
    margin-right: 10px;
    border-top: none;
}

.descricao div{

    padding-top: 10px;
    padding-bottom: 10px;
}

.descricao{
    display: none;
    font-family: 'Lato', sans-serif;
    border: 2px black solid;
    font-size: x-large;
    min-height: 50px;
    
}
 



.buttons{
    min-height: 67px;
    width: 67px;
    display: none;
   float: right;
    color: white;
    background-color: red;
    margin-right: 0%;
    /* padding-top: 30px; */

}

.title{
    width: 80%;
    text-align: center;
    font-size:x-large
}

.deadline{
   margin-left: 0%;
   padding-left: 0px;
    border-right: 1px solid black;
    width: 20%;
    min-height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
}

